.button {
    position: absolute;
    top: 50%;
    right: 5px;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    border: 0;
    padding: 0;
    background-color: transparent;
    z-index: 2;
}
.button:hover{
    cursor: pointer;
}
.button::-moz-focus-inner {
    border: 0;
}


